import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IncomeTaxComponent } from './income-tax/income-tax.component';
import { InssTaxComponent } from './inss-tax/inss-tax.component';
import { AboutComponent } from './about/about.component';

const routes: Routes = [
  { path: '', redirectTo: '/incometax', pathMatch: 'full' },
  { path: 'incometax', component: IncomeTaxComponent },
  { path: 'inss', component: InssTaxComponent },
  { path: 'about', component: AboutComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
